// withPinLocation.js
import React, { useState, useRef, useEffect } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Typography,
  Button,
  Box,
} from "@mui/material";

import axios from "axios";
import { baseUrl } from "../baseUrl";
import toastMessage from "./toastMessage";

const CustomTextField = (props) => (
  <TextField
    variant="outlined"
    size="small"
    fullWidth
    InputLabelProps={{
      shrink: true,
      style: {
        color: "var(--bs-primary2)",
      },
    }}
    sx={{
      "& .MuiOutlinedInput-root": {
        height: 42,
        "& .MuiOutlinedInput-input": {
          py: 1,
          textAlign: "center",
          color: "var(--bs-primary2)",
          "&::placeholder": {
            color: "var(--bs-primary2)",
            opacity: 1,
          },
        },
        "& fieldset": {
          borderColor: "var(--bs-primary2)",
        },
        "&:hover fieldset": {
          borderColor: "var(--bs-primary2)",
        },
        "&.Mui-focused fieldset": {
          borderColor: "var(--bs-primary2)",
        },
      },
    }}
    {...props}
  />
);

const withPinLocation = (Wrapped) => (props) => {
  const [open, setOpen] = useState(false);
  const [pin, setPin] = useState("");
  const [error, setError] = useState("");
  const [coords, setCoords] = useState(null);
  const [loading, setLoading] = useState(false);

  // keeps the Promise resolvers waiting for coords
  const pendingResolvers = useRef([]);

  const ensureLocation = () =>
    new Promise((resolve, reject) => {
      pendingResolvers.current.push({ resolve, reject });
      setOpen(true);
    });

  const getLocation = async () => {
    let body = {
      pin: pin,
    };
    try {
      let res = await axios.post(
        `${baseUrl}user/verify-mobile-location-pin`,
        body,
        {
          headers: {
            "Content-type": "application/json; charset=UTF-8",
            Authorization: `Bearer ${window.localStorage.getItem("userToken")}`,
          },
        }
      );

      return res?.data;
    } catch (error) {}
  };

  const submitPin = async () => {
    if (!/^\d{4}$/.test(pin)) {
      setError("Enter a valid 4digit PIN");
      return;
    }
    setLoading(true);
    try {
      const fetched = await getLocation();
      if (fetched?.statusCode === 200) {
        setCoords(fetched.data);
        pendingResolvers.current.forEach(({ resolve }) =>
          resolve(fetched.data)
        );
        pendingResolvers.current = [];

        setTimeout(() => {
          setOpen(false);
          setPin("");
        }, 100);
      }
      toastMessage(fetched?.error, fetched?.message);

      setError("");
    } catch {
      setError("PIN not recognised or service unavailable");
    } finally {
      setLoading(false);
    }
  };

  const cancelPopup = () => {
    setCoords("");
    setOpen(false);
    setPin("");
  };

  const handleClose = () => {
    pendingResolvers.current = [];
    setOpen(false);
    setPin("");
    setError("");
  };

  return (
    <Box
      sx={{
        backgroundColor: "var(--bs-primary1)",
        color: "var(--bs-primary2)",
      }}
    >
      <Wrapped {...props} ensureLocation={ensureLocation} />

      <Dialog
        open={open}
        onClose={handleClose}
        fullWidth
        PaperProps={{
          sx: {
            borderRadius: 4,
            paddingY: 2,
            backdropFilter: "blur(10px)",
            backgroundColor: "var(--bs-primary3)",
            color: "var(--bs-primary2)",
            boxShadow: "0 8px 32px rgba(0, 0, 0, 0.2)",
            width: "100%",
            maxWidth: 350,
          },
        }}
      >
        <DialogTitle
          sx={{
            fontWeight: 600,
            textAlign: "center",
            fontSize: "12px",
          }}
        >
          {" "}
          Enter the 4digit PIN generated in your Android app.
        </DialogTitle>

        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          flexDirection={"column"}
          mb={2}
          gap={1}
          marginX={4}
        >
          <CustomTextField
            fullWidth
            label="Enter You Location Pin "
            autoFocus
            name="userenterpin"
            value={pin}
            onChange={(e) =>
              setPin(e.target.value.replace(/\D/g, "").slice(0, 4))
            }
            error={!!error}
            helperText={error}
          />
        </Box>
        <DialogActions sx={{ justifyContent: "center", mt: 2 }}>
          <Box
            sx={{
              display: "flex",
              width: "100%",
              justifyContent: "flex-end",
              columnGap: 3,
              px: 3, // optional padding on sides
            }}
          >
            <Box>
              {/* <Button
                onClick={cancelPopup}
                color="error"
                variant="outlined"
                sx={{
                  borderRadius: 2,
                  px: 4,
                  width: "150px",
                }}
              >
                Cancel
              </Button> */}
            </Box>

            <Box>
              <Button
                disabled={pin.length !== 4 || loading}
                variant="contained"
                onClick={submitPin}
                sx={{
                  backgroundColor: "var(--bs-primary3)", //  primary3 background
                  color: "var(--bs-primary2)", //  primary2 text
                  borderRadius: 2,
                  px: 4,
                  width: "150px",
                  "&:hover": {
                    backgroundColor: "var(--bs-primary3)", // keep hover same as background
                    opacity: 0.9,
                  },
                }}
              >
                Submit
              </Button>
            </Box>
          </Box>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default withPinLocation;
